















































































.body_bg {
  background: url(../assets/site_bg.jpg) no-repeat !important;
  background-size: auto 100%;
  background-attachment: fixed;
}
.red_my_user_logo {
  width: 10em;
  height: 10em;
  border-radius: 100%;
  border: 2px solid #fff;
  text-align: center;
  margin: 0 auto 0;
  overflow: hidden;
  vertical-align: middle;
}
.red_my_user_logo > img {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  margin-top: 5px;
}
.red_my_user_name {
  text-align: center;
  font-size: 26px;
}
.red_my_user_info {
  background: #fbf9fe;
  padding: 10px 0;
}
.red-card-flex {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.red-card-flex > div {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  padding: 10px 0;
  text-align: center;
}
.red-card-flex > div > span {
  color: red;
  font-size: 14px;
}
.red-my-icon {
  margin-right: 5px;
}



