  margin-top: -50px;
}
.red-box-fix .red-gift-body {
  margin-top: -70px;
}
.red-gift-img {
  background-repeat: no-repeat;
  background-position: center center;
  height: 240px;
  margin-top: -5px;
  background-size: cover;
  position: relative;
}
.red-gift-img > .red-gift-inner {
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.75);
  -webkit-backdrop-filter: blur(6px);
          backdrop-filter: blur(6px);
  text-align: center;
}
.red-gift-inner > img {
  display: inline-block;
  width: 150px;
  height: 150px;
  margin-top: 45px;
  border-radius: 100%;
  box-shadow: 0 0 20px rgba(0,0,0,0.75);
}
.red-gift-inner > a {
  position: absolute;
  right: 0;
  top: 10px;
  color: #fff;
  width: 35px;
}
.red-box-fix .red-gift-inner > a {
  top: 20px;
}



