






























































































































































































































.btn-blueer {
  background: #58b7ff;
  color: #fff !important;
}
.btn-blueer:hover, .btn-blueer:focus {
  background: #20a0ff;
}
.btn-blueer:active {
  background: #1d8ce0 !important;
}
.weui_cells_radio .weui_check:checked + .weui_icon_checked:before {
  color: #20a0ff !important;
}
.weui_cells_title {
  margin-top: 1.5em !important;
}
.red-paypal-item {
  padding: 10px 15px;
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  box-align: center;
  flex-align: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.red-paypal-item:before {
  content: " ";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 1px;
  border-top: 1px solid #D9D9D9;
  color: #D9D9D9;
  -webkit-transform-origin: 0 0;
          transform-origin: 0 0;
  -webkit-transform: scaleY(0.5);
          transform: scaleY(0.5);
  left: 15px;
}
.red-paypal-item-selected:after {
  content: '\ea08';
  font-size: 16px;
  color: #20a0ff;
  font-family: "weui";
  position: absolute;
  right: 15px;
  top: 12px;
}
.red-paypal-item-coin {
  font-size: 16px;
  color: #ffb026;
  padding-right: 5px;
}
.red-item-subtitle {
  font-size: 14px;
  color: #666;
}
.red-item-title > i {
  font-style: normal;
  width: 105px;
  display: inline-block;
}
.red-item-title > span {
  margin-left: 10px;
}
.red-item-cell {
  padding-right: 25px;
}
.red-item-cell .weui_cell_ft:after {
  position: absolute;
  display: block;
  top: 18px;
  right: 10px;
}



